/**
* Eric Meyer's Reset CSS v2.0 (http://meyerweb.com/eric/tools/css/reset/)
* http://cssreset.com
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  1% {
    opacity: 0;
    visibility: visible;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  1% {
    opacity: 0;
    visibility: visible;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  99% {
    opacity: 0;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  99% {
    opacity: 0;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes highlight {
  0% {
    background: #ffff99;
  }
  100% {
    background: none;
  }
}
@-moz-keyframes highlight {
  0% {
    background: #ffff99;
  }
  100% {
    background: none;
  }
}
@-o-keyframes highlight {
  0% {
    background: #ffff99;
  }
  100% {
    background: none;
  }
}
@keyframes highlight {
  0% {
    background: #ffff99;
  }
  100% {
    background: none;
  }
}
@keyframes modalEnter {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  1% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateY(-150px);
    -moz-transform: translateY(-150px);
    -o-transform: translateY(-150px);
    -ms-transform: translateY(-150px);
    transform: translateY(-150px);
  }
  100% {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0px);
    -moz-transform: translateY(0px);
    -o-transform: translateY(0px);
    -ms-transform: translateY(0px);
    transform: translateY(0px);
  }
}
@-webkit-keyframes modalEnter {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  1% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateY(-150px);
  }
  100% {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0);
  }
}
@keyframes modalExit {
  0% {
    opacity: 1;
    visibility: visible;
  }
  99% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateY(150px);
    -moz-transform: translateY(150px);
    -o-transform: translateY(150px);
    -ms-transform: translateY(150px);
    transform: translateY(150px);
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes modalExit {
  0% {
    opacity: 1;
    visibility: visible;
  }
  99% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateY(150px);
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
body {
  margin: 0;
  font-size: 10pt;
  font-family: "Roboto", "HelveticaNeue-Light", "sans-serif";
  color: #444444;
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  line-height: 1rem;
  padding: 0;
  overflow: visible;
  position: absolute;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
input:focus {
  outline: none;
  box-shadow: 0 0 0;
}
code {
  border-radius: 4px;
  border: 1px solid;
  display: block;
  font-size: 13px;
  font-weight: bold;
  margin: 5px 0;
  padding: 0.5em;
  vertical-align: middle;
  word-wrap: break-word;
  text-align: left;
  white-space: pre-wrap;
  border-color: #ccc;
  color: #333;
  background-color: #f8f8f8;
}
code.inline {
  display: inline;
  padding: 0 0.5em;
  margin: 0 0.1em;
  line-height: 16px;
  font-family: monospace;
}
.livechat-room {
  position: fixed;
  top: 0;
  bottom: 0;
}
.livechat-room .title {
  color: #FFF;
  position: absolute;
  top: 0;
  width: 100%;
  height: 30px;
  display: table;
  z-index: 10;
  cursor: pointer;
  margin: auto;
  text-align: left;
}
.livechat-room .title h1 {
  margin: 0;
  padding: 0 5px;
  font-size: 9pt;
  display: table-cell;
  vertical-align: middle;
}
.livechat-room .title span {
  /*position: absolute;
      right: 15px;
      color: #ffff00;*/
}
.livechat-room .user {
  font-weight: 600;
  color: #444444;
  margin-right: 5px;
  outline: none;
}
.livechat-room .user:hover {
  color: #333;
}
.livechat-room .messages {
  display: block;
  background-color: #FFF;
  top: 30px;
  bottom: 144px !important;
  border-left: 1px solid #E7E7E7;
  border-right: 1px solid #E7E7E7;
  position: relative;
  height: calc(100% - 90px);
  width: 100%;
}
.livechat-room .messages .wrapper {
  height: 100%;
  overflow-y: auto;
  padding-bottom: 6px;
}
.livechat-room .messages .wrapper ul {
  list-style-type: none;
  padding: 0;
}
.livechat-room .messages .wrapper ul li {
  padding: 0;
}
.livechat-room .messages .wrapper .message {
  font-size: 12px;
  padding-left: 40px;
  position: relative;
  line-height: 18px;
  margin: 25px 65px 10px 10px;
  min-height: 36px;
}
.livechat-room .messages .wrapper .message .image {
  max-height: 200px;
  max-width: 100%;
}
.livechat-room .messages .wrapper .message .local-image {
  max-height: 200px;
  max-width: 100%;
}
.livechat-room .messages .wrapper .message .msgBody {
  background-color: #F9F9F9;
  padding: 3px;
  border-radius: 3px;
  text-align: left;
  color: #777777;
  border: 1px solid #E8E8E8;
  font-size: 12px;
  position: relative;
  max-width: 190px;
}
.livechat-room .messages .wrapper .message .msgBody .msgExtend {
  position: absolute;
  right: -77px;
  width: 55px;
  color: #888;
  cursor: default;
}
.livechat-room .messages .wrapper .message .msgBody .msgExtend .unRead {
  width: 11px;
  height: 11px;
  vertical-align: middle;
  display: inline-block;
}
.livechat-room .messages .wrapper .message .msgBody .audioIcon {
  width: 23px;
  height: 23px;
  vertical-align: middle;
  display: inline-block;
  background-position: 0 -2427px;
}
.livechat-room .messages .wrapper .message .audioMsg {
  cursor: pointer;
}
.livechat-room .messages .wrapper .message .arrowBorder {
  position: absolute;
  top: 25px;
  width: 0px;
  font-size: 0px;
  line-height: 0px;
  height: 0px;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-right: 7px solid #E8E8E8;
  left: 33px;
}
.livechat-room .messages .wrapper .message .arrow {
  position: absolute;
  top: 25px;
  width: 0px;
  font-size: 0px;
  line-height: 0px;
  height: 0px;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  left: 35px;
  border-right: 7px solid #F9F9F9;
}
.livechat-room .messages .wrapper .message:nth-child(1) {
  margin-top: 0;
}
.livechat-room .messages .wrapper .message.new-day {
  margin-top: 60px;
}
.livechat-room .messages .wrapper .message.new-day:before {
  content: attr(data-date);
  display: block;
  position: absolute;
  top: -30px;
  left: 0;
  font-size: 10px;
  font-weight: 600;
  text-align: center;
  -lh-property: 0;
left:-webkit-calc(50% - 70px);
left:-moz-calc(50% - 70px);
left:calc(50% - 70px);
;
  color: #7f7f7f;
  z-index: 10;
  padding: 0 10px;
  background-color: #FFF;
  min-width: 120px;
}
.livechat-room .messages .wrapper .message.new-day:after {
  content: " ";
  display: block;
  position: absolute;
  top: -20px;
  left: 0;
  width: 100%;
  border-top: 0px solid #fff;
}
.livechat-room .messages .wrapper .message .edit-message {
  display: none;
  cursor: pointer;
}
.livechat-room .messages .wrapper .message.own:hover:not(.system) .edit-message {
  display: inline-block;
}
.livechat-room .messages .wrapper .message .delete-message {
  display: none;
  cursor: pointer;
}
.livechat-room .messages .wrapper .message .thumb {
  position: absolute;
  left: 0;
  top: 0px;
  display: block;
  width: 30px;
  height: 30px;
}
.livechat-room .messages .wrapper .message .info {
  font-size: 10px;
  color: #999999;
  text-align: center;
  position: absolute;
  top: 0px;
  width: 100%;
  margin-top: -15px;
}
.livechat-room .messages .wrapper .message .info .time {
  background-color: #eeeeee;
  border-radius: 3px;
  padding: 3px 3px 0 3px;
}
.livechat-room .messages .wrapper .message.sequential .info .time {
  display: none;
}
.livechat-room .messages .wrapper .message.own {
  text-align: right;
  margin-right: 65px;
}
.livechat-room .messages .wrapper .message.own .thumb {
  right: -40px;
  left: auto;
  top: 0px;
}
.livechat-room .messages .wrapper .message.own .info {
  right: auto;
}
.livechat-room .messages .wrapper .message.own .msgBody {
  background-color: #b2e281;
  padding: 3px;
  border-radius: 3px;
  color: #777777;
  border: 1px solid #b2e281;
  text-align: right;
  right: 0px;
  margin-left: auto;
  font-size: 12px;
}
.livechat-room .messages .wrapper .message.own .msgBody .msgExtend {
  left: -80px;
  text-align: right;
}
.livechat-room .messages .wrapper .message.own .msgBody .audioIcon {
  background-position: 0 -2450px;
}
.livechat-room .messages .wrapper .message.own .arrowBorder {
  position: absolute;
  top: 25px;
  width: 0px;
  right: -7px;
  font-size: 0px;
  line-height: 0px;
  height: 0px;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-left: 7px solid #E8E8E8;
  border-right: inherit;
  left: inherit;
}
.livechat-room .messages .wrapper .message.own .arrow {
  position: absolute;
  top: 25px;
  width: 0px;
  font-size: 0px;
  line-height: 0px;
  height: 0px;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-left: 7px solid #b2e281;
  right: -6px;
  border-right: inherit;
  left: inherit;
}
.livechat-room .messages .wrapper .message.own:hover:not(.system) .delete-message {
  display: inline-block;
}
.livechat-room .messages .wrapper .message.system .body {
  color: #aaaaaa;
  font-style: italic;
  text-transform: lowercase;
}
.livechat-room .messages .wrapper .message.system .body em {
  font-weight: 600;
}
.livechat-room .messages .wrapper .message .avatar-initials {
  line-height: 40px;
}
.livechat-room .messages .wrapper .message a {
  color: #008ce3;
  font-weight: 400;
}
.livechat-room .messages .wrapper .message a:hover {
  color: #006db0;
  text-decoration: underline;
}
.livechat-room .messages .wrapper .message .body {
  opacity: 1;
  -webkit-transition: opacity 1s linear;
  -moz-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
  width: 100%;
}
.livechat-room .messages .wrapper .message.temp .body {
  opacity: .5;
}
.livechat-room .messages .wrapper .message.msg-error .body {
  text-decoration: line-through;
}
.livechat-room .messages .wrapper .message .avatar .avatar-image {
  height: 100%;
  width: 100%;
  min-height: 20px;
  min-width: 20px;
  display: block;
  position: relative;
  background-color: transparent;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 4px;
}
.livechat-room .messages .wrapper .message audio {
  min-width: 250px;
}
.livechat-room .messages .new-message {
  margin: 0 -65px;
  position: absolute;
  background: #428bca;
  border-radius: 20px;
  width: 130px;
  height: 30px;
  text-align: center;
  color: #FFF;
  line-height: 30px;
  font-size: 0.8em;
  cursor: pointer;
  bottom: 8px;
  left: 50%;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  -moz-transition: -moz-transform 0.3s ease-out;
  -o-transition: -o-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out,-moz-transform 0.3s ease-out,-o-transform 0.3s ease-out,transform 0.3s ease-out;
  -webkit-transform: translateY(0px);
  -moz-transform: translateY(0px);
  -o-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px);
}
.livechat-room .messages .new-message.not {
  -webkit-transform: translateY(150%);
  -moz-transform: translateY(150%);
  -o-transform: translateY(150%);
  -ms-transform: translateY(150%);
  transform: translateY(150%);
  filter: alpha(opacity=0);
}
.livechat-room .messages .error {
  top: -26px;
  position: fixed;
  width: 100%;
  background-color: #F7D799;
  padding: 0;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  -moz-transition: -moz-transform 0.3s ease-out;
  -o-transition: -o-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out,-moz-transform 0.3s ease-out,-o-transform 0.3s ease-out,transform 0.3s ease-out;
  -webkit-transform: translateY(200%);
  -moz-transform: translateY(200%);
  -o-transform: translateY(200%);
  -ms-transform: translateY(200%);
  transform: translateY(200%);
}
.livechat-room .messages .error.show {
  -webkit-transform: translateY(8px);
  -moz-transform: translateY(8px);
  -o-transform: translateY(8px);
  -ms-transform: translateY(8px);
  transform: translateY(8px);
}
