body {
  background-color: #999999;
}

.livechat-room {
  width: 100%;
  .xCover {
    z-index: 20;
    position: fixed;
    width: 100%;
    top: 0px;
    height: 100%;
    display: none;
    background: rgba(160, 163, 165, 0.7);
    filter: alpha(opacity=100);
    .xContent {
      position: fixed;
      width: 100%;
      height: 100%;
      background: #ffffff none repeat scroll 0% 0%;
      img.btn_close {
        position: absolute;
        right: 7px;
        top: 5px;
        cursor: pointer;
      }
      .infotext {
        position: fixed;
        top: 46px;
        height: 80px;
        width: 100%;
        line-height: 80px;
        font-size: 15px;
        text-align: center;
        color: #777777;
      }
      .btn-area {
        position: fixed;
        top: 126px;
        width: 100%;
        height: 50%;
        border-top: 1px solid #eeeeee;
        border-bottom: 1px solid #eeeeee;
        ul {
          width: 100%;
          margin-left: 60px;
          font-size: 13px;
          li {
            margin: 7% 0 7% 0;
            color: #666666;
          }
          li.btns_area {
            padding: 25px 0 15px 18px;
            cursor: pointer;
            input[type=button] {
              color: #666666;
              width: 80px;
              &.btn_submit {
                background-color: #444850;
                border: none;
                color: white;
              }
              &.btn_cancel {
                margin-left: 84px;
                margin-right: 10px;
                width: 80px;
                border: 1px solid #cbc8c8;
                background-color: #ffffff;
              }
            }
          }
        }
      }
      .survey-submit{
        position: fixed;
        bottom: 0px;
        height: 15%;
        width: 100%;
        text-align: center;
        .cancel{
          width: 40%;
          border: 1px solid #eeeeee;
          background-color: #ffffff;
          height: 30px;
          font-size: 15px;
          color: #777777;
        }
        .submit{
          width: 40%;
          border: 1px solid #eeeeee;
          background-color: #ffffff;
          height: 30px;
          font-size: 15px;
          color: #777777;
        }
      }
      .agent-survey{
        .cancel{
          display: none;
        }
        .submit{
          width: 80%;
        }
      }

    }
  }
  .tool_banner {
    padding-top: 5px;
    .file-uploader {
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-left: 7px;
      cursor: pointer;
      input {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 16px;
        height: 16px;
        overflow: hidden;
        opacity: 0;
        filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
        cursor: pointer;
      }
    }
    a {
      padding-left: 7px;
      cursor: pointer;
    }
  }
  .tool_btns {
    margin-bottom: 20px;
    padding: 7px;
    height: 20px;
    text-align: right;
    a {
      display: inline-block;
      width: 80px;
      height: 20px;
      text-decoration: none;
      color: grey;
      background-color: white;
      text-align: center;
      border: 1px solid grey;
      cursor: pointer;
    }
  }
  .title {
    h1 {
      padding-left: 30px !important;
    }
    img {
      height: 18px;
      width: 18px;
      position: absolute;
    }
    img.logo {
      left: 5px;
      top: 6px;
    }
    img.msg {
      right: 15px;
      top: 14px;
    }
    .msgcount {
      display: none;
      position: absolute;
      top: 3px;
      right: 10px;
      width: 16px;
      height: 16px;
      text-align: center;
      background-color: #ff0000;
      border-radius: 12px;
      span {
        padding-right: 2px;
        font-weight: bold;
      }
    }
  }
  .messages {
    div.show {
      top: 22px;
      left: 0px;
    }
    div.msgTime {
      position: fixed;
      left: 0;
      top: 30px;
      width: 100%;
      text-align: center;
      background-color: #e3e7ea;
    }
    .hideMsgTime {
      top: 10px !important;
    }
  }
}