@import "_variables.less";
@import "utils/_lesshat.import.less";
@import "utils/_reset.import.less";
@import "utils/_keyframes.import.less";

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-size: 10pt;
  font-family: "Roboto", "HelveticaNeue-Light", "sans-serif";
  // font-size: 14px;
  color: @primary-font-color;
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  line-height: 1rem;
  padding: 0;
  overflow: visible;
  position: absolute;
  // background-color: @primary-background-color;
}

input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input:focus {
  outline: none;
  box-shadow: 0 0 0;
}

code {
	border-radius: 4px;
	border: 1px solid;
	display: block;
	font-size: 13px;
	font-weight: bold;
	margin: 5px 0;
	padding: 0.5em;
	vertical-align: middle;
	word-wrap: break-word;
	text-align: left;
	white-space: pre-wrap;
	border-color: #ccc;
	color: #333;
	background-color: #f8f8f8;
	&.inline {
		display: inline;
		padding: 0 0.5em;
		margin: 0 0.1em;
		line-height: 16px;
		font-family: monospace;
	}
}

.livechat-room {
  position: fixed;
  top: 0;
  bottom: 0;
  .title {
    color: #FFF;
    position: absolute;
    top: 0;
    width: 100%;
    height: @header-min-height;
    display: table;
    z-index: 10;
    cursor: pointer;
    margin: auto;
    text-align: left;
    h1 {
      margin: 0;
      padding: 0 5px;
      font-size: 9pt;
      display: table-cell;
      vertical-align: middle;
    }
    span {
      /*position: absolute;
      right: 15px;
      color: #ffff00;*/
    }
  }
  .user {
    font-weight: 600;
    color: #444444;
    margin-right: 5px;
    outline: none;
    &:hover {
      color: #333;
    }
  }
  .messages {
    display: block;
    background-color: #FFF;
    top: @header-min-height;
    bottom: 144px !important;
    border-left: 1px solid #E7E7E7;
    border-right: 1px solid #E7E7E7;
    position: relative;
    height:~"calc(100% - 90px)";
    width: 100%;
    .wrapper {
      height: 100%;
      overflow-y: auto;
      padding-bottom: 6px;

      ul {
        list-style-type: none;
        padding: 0;
        li {
          padding: 0;
        }
      }

      .message {
        font-size: 12px;
        padding-left: 40px;
        position: relative;
        line-height: 18px;
        margin: 25px 65px 10px 10px;
        min-height: 36px;
        .image {
          max-height: 200px;
          max-width: 100%;
        }
        .local-image {
          max-height: 200px;
          max-width: 100%;
        }
        .msgBody{
          background-color: #F9F9F9;
          padding: 3px;
          border-radius: 3px;
          //margin:0 30% 0 0;
          text-align: left;
          color: #777777;
          border: 1px solid #E8E8E8;
          font-size: 12px;
          position: relative;
          max-width: 190px;

          .msgExtend{
            position: absolute;
            right: -77px;
            width: 55px;
            color: #888;
            cursor: default;

            .unRead{
              width: 11px;
              height: 11px;
              vertical-align: middle;
              display: inline-block;
            }
          }

          .audioIcon{
            width: 23px;
            height: 23px;
            vertical-align: middle;
            display: inline-block;
            background-position: 0 -2427px;
          }

        }
        .audioMsg{
          cursor: pointer;
        }
        .arrowBorder{
          position: absolute;
          top: 25px;
          width: 0px;
          font-size: 0px;
          line-height: 0px;
          height: 0px;
          border-bottom: 5px solid transparent;
          border-top: 5px solid transparent;
          border-right: 7px solid #E8E8E8;
          left: 33px;
        }
        .arrow {
          position: absolute;
          top: 25px;
          width: 0px;
          font-size: 0px;
          line-height: 0px;
          height: 0px;
          border-bottom: 5px solid transparent;
          border-top: 5px solid transparent;
          left: 35px;
          border-right: 7px solid #F9F9F9;
        }
        //.user {
        //  position: absolute;
        //  word-break:break-all;
        //  left: -5px;
        //  width: 40px;
        //  top: 30px;
        //  text-align: center;
        //
        //}
        &:nth-child(1) {
          margin-top: 0;
        }
        &.new-day {
          margin-top: 60px;
        }
        &.new-day {
          &:before {
            content: attr(data-date);
            display: block;
            position: absolute;
            top: -30px;
            left: 0;
            font-size: 10px;
            font-weight: 600;
            text-align: center;
            .calc(left, ~'50% - 70px');
            color: @secondary-font-color;
            z-index: 10;
            padding: 0 10px;
            background-color: #FFF;
            min-width: 120px;
          }
          &:after {
            content: " ";
            display: block;
            position: absolute;
            top: -20px;
            left: 0;
            width: 100%;
            border-top: 0px solid #fff;
          }
        }
        .edit-message {
          display: none;
          cursor: pointer;
        }
        &.own:hover:not(.system) .edit-message {
          display: inline-block;
        }
        .delete-message {
          display: none;
          cursor: pointer;
        }
        .thumb {
          position: absolute;
          left: 0;
          top: 0px;
          display: block;
          width: 30px;
          height: 30px;
        }
        .info {
          font-size: 10px;
          color: #999999;
          text-align: center;
          position: absolute;
          top: 0px;
          width: 100%;
          margin-top: -15px;
          .time{
            background-color: #eeeeee;
            border-radius: 3px;
            padding: 3px 3px 0 3px;
          }
        }
        &.sequential {
          .info {
            .time {
              display: none;
            }
          }
          // margin-top: 5px;
          //padding-top: 5px;
          //margin-top: 0;
          //margin-bottom: 0;
          //min-height: 20px;
          //.user {
          //  display: none;
          //}
          //.thumb {
          //  display: none;
          //}
          //.info {
          //  position: absolute;
          //  text-align: right;
          //  left: -20px;
          //  width: 55px;
          //  .time {
          //    display: none;
          //  }
          //  .edited {
          //    display: inline-block;
          //  }
          //  .edit-message {
          //    float: left;
          //    margin-left: 1px;
          //  }
          //  .delete-message {
          //    float: left;
          //  }
          //}
          //&:hover {
          //  .time {
          //    display: inline-block;
          //  }
          //  .edited {
          //    display: none;
          //  }
          //}
        }
        &.own {
          text-align: right;
          margin-right: 65px;
          .thumb {
            right: -40px;
            left: auto;
            top: 0px;
          }
          .info{
            right:auto;
          }
          .msgBody{
            background-color: #b2e281;
            padding: 3px;
            border-radius: 3px;
            color: #777777;
            border: 1px solid #b2e281;
            text-align: right;
            right: 0px;
            margin-left: auto;
            font-size: 12px;

            .msgExtend{
              left: -80px;
              text-align: right;
            }

            .audioIcon{
              background-position: 0 -2450px;
            }
          }
          //.user {
          //  position: absolute;
          //  left: inherit;
          //  right: -60px;
          //}
          .arrowBorder{
            position: absolute;
            top: 25px;
            width: 0px;
            right: -7px;
            font-size: 0px;
            line-height: 0px;
            height: 0px;
            border-bottom: 5px solid transparent;
            border-top: 5px solid transparent;
            border-left: 7px solid #E8E8E8;
            border-right: inherit;
            left: inherit;
          }
          .arrow{
            position: absolute;
            top: 25px;
            width: 0px;
            font-size: 0px;
            line-height: 0px;
            height: 0px;
            border-bottom: 5px solid transparent;
            border-top: 5px solid transparent;
            border-left: 7px solid #b2e281;
            right: -6px;
            border-right: inherit;
            left: inherit;
          }
        }
        &.own:hover:not(.system) .delete-message {
          display: inline-block;
        }
        &.system {
          .body {
            color: @info-font-color;
            font-style: italic;
            text-transform: lowercase;
            em {
              font-weight: 600;
            }
          }
        }
        .avatar-initials {
          line-height: 40px;
        }
        a {
          color: @link-font-color;
          font-weight: 400;
          &:hover {
            color: darken(@link-font-color, 10%);
            text-decoration: underline;
          }
        }
        .body {
          opacity: 1;
          .transition(opacity 1s linear);
          width:100%;
        }
        &.temp .body {
          opacity: .5;
        }
        &.msg-error .body {
          text-decoration: line-through;
        }

        .avatar .avatar-image {
          height: 100%;
          width: 100%;
          min-height: 20px;
          min-width: 20px;
          display: block;
          position: relative;
          background-color: transparent;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
          border-radius: 4px;
        }
        audio{
          min-width: 250px;
        }
      }
    }
    .new-message {
      margin: 0 -65px;
      position: absolute;
      background: #428bca;
      border-radius: 20px;
      width: 130px;
      height: 30px;
      text-align: center;
      color: #FFF;
      line-height: 30px;
      font-size: 0.8em;
      cursor: pointer;
      bottom: 8px;
      left: 50%;
      .transition(transform 0.3s ease-out);
      .transform(translateY(0));
      &.not {
        .transform(translateY(150%));
        filter:alpha(opacity=0)
      }
    }

    .error {
      top: -26px;
      position: fixed;
      width: 100%;
      background-color: #F7D799;
      padding: 0;

      .transition(transform 0.3s ease-out);
      .transform(translateY(200%));

      &.show {
        .transform(translateY(8px));
      }
    }
  }
  .footer {
    display: block;
    background-color: #FCFCFC;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: auto;
    border-top: 1px solid #E7E7E7;
    border-left: 1px solid #E7E7E7;
    border-right: 1px solid #E7E7E7;

    .input-wrapper {
      padding: 6px;
      height:60px;
      textarea {
        display: inline-block;
        padding: 6px 8px;
        padding-right: 38px;
        overflow-y: auto;
        resize: none;
        border: 1px solid #E7E7E7;
        // margin: 10px;
        border-radius: 5px;
        max-height: 200px;
        width: 100%;
        font-size: 12px;
        -webkit-appearance: none;
        height: 40px;
        line-height: 24px;
        background-color: #fff;
        position: relative;
        outline: none;
      }
      .file-uploader{
        input{
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 40px;
          height: 40px;
          overflow: hidden;
          opacity: 0;
          filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
          cursor: pointer;
        }
      }
    }
  }
}

.intro-msg{
  margin-top: 30px;
  font-size: 1.2rem;
  line-height: 1.4rem;
  text-align: center;
  padding: 10px;

  p{
    max-width:80%;
    margin: auto;
  }
}

.hide{
  display: none;
}

.show{
  display: block;
}

.bar{
  .btn-link{
    color: #fff;
  }
}
.bar-nav{
  .title{
    color: #fff;
    text-align: center;
  }
}

.livechat-room{
  .bar-nav{
    height: 46px;
  }
}
.bar.confirm{
  border: none;
  margin-top: 10%;
}

.ticket{
  textarea {
    resize: none;
    border: none;
    height: 100%;
    padding: 10px 15px;
  }
  .ticket-messages{
    height: 60%;
  }
  .alert {
    color: red;
  }
  .submit{
    border: none;
  }
  .msg-error{
    color: red;
    height: 16px;
    padding-left: 15px;
    margin-bottom: 0;
  }
}

.result{
  .content{
    div{
      text-align: center;
    }
    .result-icon{
      margin-top: 100px;

      .icon-large{
        width: 80px;
        height: 80px;
        margin: 3px;
        font-size: 70px;
        line-height: 80px;
        text-align: center;
        background-color: #ddd;
        border: 1px solid #ddd;
        border-radius: 40px;
        color: #fff;
      }
    }
  }
}

.rating{
  .rating-list{
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 10px 10px 10px 40px;
    font-size:1.1rem;
    line-height: 3rem;
    input{
      margin-right: 10px;
    }
  }
}


.web-browser .livechat-room {
  .title {
   height: @web-header-height;
  }
}
.web-browser .messages {
  top: @web-header-height;
  bottom: 60px !important;
  position: fixed;
  height: auto;
}
.web-browser .footer .input-wrapper{
  padding: 6px;
  height:60px;
  textarea {
    position: fixed;
    width:320px;
  }
  .file-uploader .old-ie-notice{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 40px;
    height: 40px;
    overflow: hidden;
    opacity: 0;
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
    cursor: pointer;
  }
}
.web-browser .system-notice{
  position: fixed;
  bottom: 65px;
  right: 5px;
  height: 80px;
  width: 200px;
  border: 1px solid #eeeeee;
  background-color: #ffffff;
  .system-notice-closer{
    cursor: pointer;
    width: 12px;
    height: 12px;
    FLOAT: right;
    MARGIN: 10px 10px 0px 0px;
    background-color: #00caed;
  }
  .system-notice-icon{
    width: 44px;
    height: 44px;
    FLOAT: left;
    MARGIN: 20px 0px 0px 10px;
    background-color: #00caed;
  }
  .system-notice-text{
    FLOAT: right;
    height: 44px;
    MARGIN: 10px 5px 0px 0px;
    color:#777777;
    font-size:12px;
  }
  .system-notice-arrow1{
    position: fixed;
    width: 0px;
    height: 0px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #eeeeee;
    font-size: 0px;
    line-height: 0px;
    bottom: 55px;
    right: 27px;
  }
  .system-notice-arrow2{
    position: fixed;
    width: 0px;
    height: 0px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #ffffff;
    font-size: 0px;
    line-height: 0px;
    bottom: 57px;
    right: 27px;
  }
}

.cordova{
  .footer{
    .input-wrapper{
      padding: 6px;
      height:60px;
      textarea {
        width:~"calc(100% - 60px)";
      }
    }
  }
  .livechat-room{
    position: absolute;
  }
}

@media all and(max-height: 200px) {
  .livechat-room {
    .title {
      height: 100%;
    }
    .footer {
      display: none;
    }
    .messages {
      display: none;
    }
  }
}
